import React from 'react';
import '../css/sidebar.css'

const Sidebar = (props) => {

    // const closeSidebar = () =>{
    //     props.sidebarUpdate('closed')
    // }
    var boxWidth = '0px'
    var boxDisplay = 'none'

    if(props.sidebar === 'opened'){
       boxWidth = '300px'
       boxDisplay = 'block'
    } else if (props.sidebar === 'closed'){
        boxWidth = '0px'
       boxDisplay = 'none'
    }

    return (
        <div>
                <div  style = {{width:boxWidth}}className= 'sidebarContainer' >
                <i onClick = {(event) => props.sidebarUpdate('closed')} className="fi-xtluxl-times-thin" aria-hidden="true" > </i>
                <ul>
                    <li onClick = {(event) => props.sidebarUpdate('closed')}><i class="fi-unsuxl-file-solid"></i>My Notes</li>
                    <li onClick = {(event) => props.sidebarUpdate('closed')}><i class="fi-unsuxl-bell-solid"></i>Reminder</li>
                    <li onClick = {(event) => props.sidebarUpdate('closed')}><i class="fi-unsuxl-trash-bin"></i>Bin</li>
                    <li onClick = {(event) => props.sidebarUpdate('closed')}><i class="fi-unsuxl-info-solid"></i>About Us</li>
                    <li onClick = {(event) => props.sidebarUpdate('closed')}><i class="fi-unsuxl-sign-out-solid"></i>Logout</li>
                    
                </ul>
                </div>

                <div style = {{display:boxDisplay}}className = 'fullscreen' onClick = {(event) => props.sidebarUpdate('closed')}></div>

            </div>
    )
}


export default Sidebar;