import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import JSON from './notes.json'
import Sidebar from './page-componentss/sidebar';

//Components
import InputNotes from './inputNote'
import Header from './page-componentss/header';


class App extends Component {
   state = {
      JSONnotes : JSON,
      sidebar:'closed'
   }

// deletes All notes in the Array 
//Unused for now
   // deleteAll = () => {
   //       this.setState({
   //          JSONnotes:[]
   //       })
   // }

   sidebarUpdate = (value) => {
      this.setState({
         sidebar:value
      })
   }
     
   
  
   render(){
      return (
         <div>
            <Header sidebar = {this.state.sidebar} sidebarUpdate = {this.sidebarUpdate}/>
            <Sidebar sidebar = {this.state.sidebar} sidebarUpdate = {this.sidebarUpdate}/>
            <InputNotes deleteAll = {this.deleteAll} JSONnotes={this.state.JSONnotes}/>
         </div>
         
      )
   }
   }



ReactDOM.render(<App/>,document.querySelector('#root'))